@import "./src/mixines/MyMixines.module";
.messages {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  height: 100%;
  width: 80%;
  > :nth-child(1) {
    background: #12c2e9;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #f64f59, #c471ed, #12c2e9);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #f64f59, #c471ed, #12c2e9); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 10%;

      > div:has(:not(button)) {
        display: flex;
        align-items: center;
        justify-content: space-between;
        height: 100%;
        width: 50%;
        > div {
          height: 80%;
          border-radius: 100%;
          overflow: hidden;
          margin-left: 5%;
          > img {
            position: relative;
            left: 0;
            top: 0;
            height: 100%;
          }
        }

      > p {
        margin: 0 0 0 5%;
        height: auto;
      }
    }
    >div:has(button) {
      width: 8%;
      margin-right: 5%;
      @include containerForButtonBluePink;
    }
    > button {

    }
  }
  > :nth-child(2) {
    display: flex;
    flex-direction: column;
    width: 100%;
    overflow-y: scroll;
    > :nth-child(1):not(:has(img)) {
      text-align: center;
      background-color: #bdb0eb;
      border: 1px solid black;
      padding: 5%;
      font-size: xx-large;
      border-radius: 2vw 0;
    }
  }
  > :nth-child(3) {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: row;
    height: 10%;
    width: 100%;
    > textarea {
      border: 1px solid black;
      width: 80%;
      border-radius: 5px;
      padding: 2vw;
    }
    > button {
      color: white;
      width: 10%;
      height: 50%;
      border-radius: 1vw;
      background: radial-gradient(circle at 100% 100%, #771ce8 0, #771ce8 3px, transparent 3px) 0% 0%/8px 8px no-repeat,
      radial-gradient(circle at 0 100%, #771ce8 0, #771ce8 3px, transparent 3px) 100% 0%/8px 8px no-repeat,
      radial-gradient(circle at 100% 0, #771ce8 0, #771ce8 3px, transparent 3px) 0% 100%/8px 8px no-repeat,
      radial-gradient(circle at 0 0, #771ce8 0, #771ce8 3px, transparent 3px) 100% 100%/8px 8px no-repeat,
      linear-gradient(#771ce8, #771ce8) 50% 50%/calc(100% - 10px) calc(100% - 16px) no-repeat,
      linear-gradient(#771ce8, #771ce8) 50% 50%/calc(100% - 16px) calc(100% - 10px) no-repeat,
      conic-gradient(from 60deg, #ff0000 0%, #ffff00 16.67%, #00ff00 33.33%, #00ffff 50%, #0000ff 66.67%, #ff00ff 83.33%, #ff0000 100%);
    }

  }
}