.setting {
  display: flex;
  flex-direction: column;
  color: white;
  overflow: scroll;
  overflow-x: hidden;
  font-family: 'Inter', sans-serif;
  text-align: center;

  > p {
    width: 100%;
  }

  > :nth-child(1) {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: 45%;

    > img {
      position: relative;
      height: 100%;
    }

    > :nth-child(2) {
      display: flex;
      flex-wrap: wrap;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
      width: 100%;

      > button, > input {
        font-size: 14px;
        color: #fff;
        background: linear-gradient(90deg, #0066CC 0%, #c500cc 100%);
        padding: 10px 30px;
        border: 2px solid #0066cc;
        box-shadow: rgb(0, 0, 0) 0px 0px 0px 0px;
        border-radius: 50px;
        transition: 236ms;
        transform: translateY(0);
        display: flex;
        justify-content: center;
        flex-direction: row;
        align-items: center;
        cursor: pointer;
        width: 30%;
      }

      > input::after {
        border: none;
        background-color: #54b6cd;
      }

      > :hover {
        transition: 236ms;
        padding: 10px 50px;
        transform: translateY(-0px);
        background: linear-gradient(90deg, #0066CC 0%, #c500cc 100%);
        color: #e0e0e0;
        border: solid 2px #0066cc;
        width: 40%;
      }
    }
  }

  > div {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    > button, > input {
      font-size: 14px;
      color: #fff;
      background: linear-gradient(90deg, #0066CC 0%, #c500cc 100%);
      padding: 10px 30px;
      border: 2px solid #0066cc;
      box-shadow: rgb(0, 0, 0) 0px 0px 0px 0px;
      border-radius: 50px;
      transition: 236ms;
      transform: translateY(0);
      display: flex;
      justify-content: center;
      flex-direction: row;
      align-items: center;
      width: 30%;
    }
    > button {
      cursor: pointer;
    }

    > input::after {
      border: none;
      background-color: #54b6cd;
    }
  }

  > :last-child {
    flex-direction: column;
    width: 100%;
    > textarea {
      width: 80%;
      height: 10vh;
      min-height: 20%;
      font-family: 'Inter', sans-serif;
    }
  }
}