@mixin buttonBluePink {
  > button {
    font-size: 14px;
    color: #fff;
    background: linear-gradient(90deg, #0066CC 0%, #c500cc 100%);
    padding: 10px 30px;
    border: 2px solid #0066cc;
    box-shadow: rgb(0, 0, 0) 0 0 0 0;
    border-radius: 50px;
    transition: 236ms;
    transform: translateY(0);
    display: flex;
    justify-content: center;
    flex-direction: row;
    align-items: center;
    cursor: pointer;
    width: 100%;
  }
  > input::after {
    border: none;
    background-color: #54b6cd;
  }
  > :hover {
    width: 80%;
    transition: 236ms;
    padding: 10px 50px;
    transform: translateY(-0px);
    background: linear-gradient(90deg, #0066CC 0%, #c500cc 100%);
    color: #e0e0e0;
    border: solid 2px #0066cc;
  }
}

@mixin containerForButtonBluePink {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  @include buttonBluePink;
}