.dialogs {
  display: flex;
  flex-direction: column;
  align-items: center;
  > :nth-child(1):not(a) {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: xxx-large;
    margin: 0;
  }
  > :nth-child(1):not(:has(p)) {
    background-color: #bdb0eb;
    border: 1px solid black;
    padding: 5%;
    font-size: xx-large;
    border-radius: 2vw 0;
  }
}