.dialogsItem {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  height: 10vh;
  width: 100%;
  margin: 0.5% 0;
  background: rgb(131,58,180);
  background: linear-gradient(90deg, rgba(131,58,180,1) 4%, rgba(253,29,29,1) 18%, rgba(104,69,252,1) 55%);
  color: white;
  text-decoration: none;
  font-size: large;
  > section {
    display: flex;
    width: 70%;
    height: 100%;
    align-items: center;
    > div {
      display: flex;
      align-items: center;
      width: 50%;
      height: 100%;
      margin-left: 5vw;
      > div {
        border-radius: 100%;
        height: 10vh;
        width: 10vh;
        overflow: hidden;
        > img {
          width: 100%;
        }
      }
      > p {
        margin-left: 5%;
      }

    }
    > p {
      padding: 0;
      margin: 0 5%;
    }

  }
  > div:is(:last-child) {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 17%;
    > p {
      margin: 0;
    }
  }
}