@import "src/mixines/MyMixines.module.scss";
.newsCard {
  font-family: 'Oswald', sans-serif;
  display: flex;
  align-items: center;
  text-align: center;
  flex-direction: column;
  width: 96%;
  margin: 2%;
  border-radius: 2%;
  background: #C33764;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to top, #1D2671, #C33764);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to top, #1D2671, #C33764); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  text-decoration: none;
  color: white;
  height: 50%;

  > section:has(section) {
    width: 100%;
    height: 13%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    > section {
      width: 50%;
      display: flex;
      align-items: center;
      > img {
        height: 4vh;
        width: 4vh;
        margin-left: 5%;
        border-radius: 100%;
      }
      > p {
        margin-left: 5%;
      }
    }

    > div {
      @include containerForButtonBluePink;
      margin-right: 5%;
    }
  }

  > img {
    height: 70%;
  }
  > section:not(:has(section)) {
    width: 100%;
    height: 10%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    >p {
      width: 50%;
      margin: 0 0 0 5%;
    }
    > div {
      width: 30%;
      @include containerForButtonBluePink;
      margin-right: 5%;
      >button {
        > span {
          margin-left: 0.5vw;
        }
      }
    }
  }

}